import Navigation from './components/Navigation';
import Geocultures from './components/Geocultures';
import About from './components/About';
import { Route, Switch } from 'react-router-dom';
import './App.css';


function App() {
  return (
    <>
      <Navigation />
      <div className="App">
        <Switch>                
          <Route path='/geocultures' component={Geocultures}/>  
          <Route path='/about' component={About}/>
          <Route path='/' component={Geocultures}/>
        </Switch>
      </div>
    </>
  );
}

export default App;
